@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  padding: 0;
}

body {
  margin: 0;
}

button {
  border: none;
  outline: none;
}

a {
  text-decoration: none;
}

.card {
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__image {
  max-width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  padding-top: 4px;
}

.card__reviews {
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  font-size: 12px;
  display: flex;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
